import React from 'react'
import image1 from './images/landing.png'
import image2 from './images/app-react3.png'
import image3 from './images/landing-3.png'
import image4 from './images/app-react4.png'
import image5 from './images/app-react2.png'
import image6 from './images/app-react.png'
import {motion} from 'framer-motion';

const pageTransition = {
    in: {
        opacity: 1
    },
    out: {
        opacity: 0
    }
};

function Portfolio() {
    return (
        <motion.section className="portfolio" initial="out" animate="in" exit="out" variants={pageTransition}>
            <h1 className="heading">My <span>Portfolio</span></h1>
            <div className="box-container">
                <div className="box">
                    <img src={image1} alt="hola"/>
                    <div className="content">
                        <p>Project created with Html, CSS and JavaScript.</p>
                        <a href="https://luciano393.github.io/personal_trainer/" className="btn">Learn more</a>
                    </div>
                </div>
                <div className="box">
                    <img src={image2}alt="hola"/>
                    <div className="content">
                        <p>Project created with React JS.</p>
                        <a href="https://luciano393.github.io/weather-react/" class="btn">Learn more</a>
                    </div>
                </div>
                <div className="box">
                    <img src={image3}alt="hola"/>
                    <div className="content">
                        <p>Project created with Html, CSS and JavaScript.</p>
                        <a href="https://luciano393.github.io/blog/" className="btn">Learn more</a>
                    </div>
                </div>
                <div className="box">
                    <img src={image4} alt="hola"/>
                    <div className="content">
                        <p>Project created with React JS.</p>
                        <a href="https://luciano393.github.io/calculator-react/" className="btn">Learn more</a>
                    </div>
                </div>
                <div className="box">
                    <img src={image5}alt="hola"/>
                    <div className="content">
                        <p>Project created with React JS.</p>
                        <a href="https://luciano393.github.io/cotizador_de_prestamos/" className="btn">Learn more</a>
                    </div>
                </div>
                <div className="box">
                    <img src={image6} alt="hola"/>
                    <div className="content">
                    <p>Project created with React JS.</p>
                    <a href="https://luciano393.github.io/rick-and-morty-app/" className="btn">Learn more</a>
                </div>
            </div>
        </div>
        </motion.section>
    )
}

export default Portfolio
