import React from 'react'
import { IoLogoReact, IoLogoJavascript, IoLogoSass, IoLogoPython, IoLogoGithub, IoLogoCss3,} from "react-icons/io5";

function Technologies() {
    return (
        <article className="technologies">
            <h1 className="heading">My <span>technologies</span></h1>
            <div className="box-container">
                <div className="slide-track">
                    <div className="slide"><IoLogoReact className="icons"/></div>
                    <div className="slide"><IoLogoJavascript className="icons"/></div>
                    <div className="slide"><IoLogoSass className="icons"/></div>
                    <div className="slide"><IoLogoPython className="icons"/></div>
                    <div className="slide"><IoLogoCss3 className="icons"/></div>
                    <div className="slide"><IoLogoGithub className="icons"/></div>
                    <div className="slide"><IoLogoReact className="icons"/></div>
                    <div className="slide"><IoLogoJavascript className="icons"/></div>
                    <div className="slide"><IoLogoSass className="icons"/></div>
                    <div className="slide"><IoLogoPython className="icons"/></div>
                    <div className="slide"><IoLogoCss3 className="icons"/></div>
                    <div className="slide"><IoLogoGithub className="icons"/></div>
                </div>
            </div>
        </article>
    )
}

export default Technologies;
