import React from 'react'
import {motion} from 'framer-motion';

const pageTransition = {
    in: {
        opacity: 1
    },
    out: {
        opacity: 0
    }
};


function Contact() {
    return (
        <motion.section className="contact" initial="out" animate="in" exit="out" variants={pageTransition}>
            <h1 className="heading">Contact <span>me</span></h1>
            <div className="row">
                <form>
                    <div className="inputBox">
                        <input id="name" name="name" type="text" placeholder="Name" className="name" required="required"/>
                        <input id="email" name="mail" type="email" placeholder="Email" className="email" required="required"/>
                    </div>
                    <textarea placeholder="Message" name="message" id="message" cols="30" rows="10" className="message"></textarea>
                    <input type="submit" className="btn" value="Send message"/>
                </form>
            </div>
        </motion.section>
    )
}

export default Contact
