import React from 'react'
import { IoPhonePortrait, IoDesktop} from "react-icons/io5";
import {
    Link
} from 'react-router-dom';
import Technologies from './Technologies';
import {motion} from 'framer-motion';

const pageTransition = {
    in: {
        opacity: 1
    },
    out: {
        opacity: 0
    }
};


function Skills() {
    return (
        <motion.section className="skills" initial="out" animate="in" exit="out" variants={pageTransition}>
            <h1 className="heading">My <span>skills</span></h1>
            <div className="box-container">
                <div className="box">
                    <IoDesktop className="dev"/>
                    <h3>Web developer</h3>
                    <Link to="/about" className="btn">Learn more</Link>
                </div>
                <div className="box">
                    <IoPhonePortrait className="dev"/>
                    <h3>Web developer</h3>
                    <Link to="/about" className="btn">Learn more</Link>
                </div>
            </div>
            <Technologies />
        </motion.section>
    )
}

export default Skills
