import React from 'react'
import image7 from './images/foto.png'
import {
    Link
} from 'react-router-dom';
import {motion} from 'framer-motion';

const pageTransition = {
    in: {
        opacity: 1
    },
    out: {
        opacity: 0
    }
};
function Home() {
    return (
        <motion.section className="home" initial="out" animate="in" exit="out" variants={pageTransition}>
            <div className="home-container">
                <img src={image7} alt="hola"/>
                <h3>Luciano <span>Ibarra</span></h3>
                <p>I am <span>front end developer</span></p>
                <Link to="/about" className="btn">About me</Link>
            </div>
        </motion.section>
    )
}

export default Home
