import React from 'react'
import {motion} from 'framer-motion';

const pageTransition = {
    in: {
        opacity: 1
    },
    out: {
        opacity: 0
    }
};


function About() {
    return (
        <motion.section className="about" initial="out" animate="in" exit="out" variants={pageTransition}>
            <h1 className="heading">About <span>Me</span></h1>
            <div className="content">
                <h3>My name is <span>Luciano Ibarra</span></h3>
                <p className="info">I am a Bachelor of Information Systems student at the University National of Lujan and I also study Python programming at the University National of San Martin. <br/>I have a great passion for web development, that's why i keep training constantly in a self-taught way.</p>
            </div>
            <div className="box-container">
            <div className="box">
                    <p><span> Age: </span> 26 </p>
                    <p><span> Gender: </span> Male </p>
                    <p><span> Experience: </span> 1 year </p>
                    <p><span> Freelance: </span> Available</p>
                </div>
                <div class="box">
                    <p><span> Language: </span> Spanish / English basic / Portugues basic</p>
                    <p><span> Phone: </span> +549-011-2700-9501</p>
                    <p><span> Email: </span> luciano39340268@gmail.com</p>
                    <p><span> Location:</span> Buenos Aires, Argentina</p>
                </div>
            </div>
            <a href="https://drive.google.com/file/d/1EK60_usM1ELbNutwHhmxFZQHr1VfKTfU/view?usp=sharing" className="btn">Download CV</a>
        </motion.section>
    )
}

export default About
